import requireurl from '../../requist/requist.js'
import uniPopup from "../../components/uni-popup/uni-popup.vue"
export default {
  components: {uniPopup},
	data() {
		return {
			code:'',
			showcode:1,
			numbertime:59,
			time:null,
			type:'center',
			authentication:false,
			usermobile:uni.getStorageSync('storage_mobile')
		}
	},
	methods: {
		asdasd(){
			console.log('我失焦了')
		},
		
		//获取验证码
		getcode(){
			this.showcode = 1
			this.time = setInterval(()=>{
				this.numbertime--
				if(this.numbertime <= 0){
				this.showcode = 2
				this.numbertime = 59
					clearInterval(this.time)
				}
			},1000)
		},
		
		
		
		//发送验证码
		sedeCode(){
			let data = {
				mobile:uni.getStorageSync('storage_mobile'),
				code : this.code
			}
			requireurl.request('/users/login/',data, this.getCoodesuc, this.getFile)
		},
		
		//发送验证码回调
		getCoodesuc(e){
			if(e.statusCode === 201){
				console.log('登录成功')
				uni.removeStorage({
				    key: 'storage_mobile',
				    success: function (res) {
						uni.setStorageSync('storage_user', e.data);
						uni.reLaunch({
						    url: '../index/index'
						});
				    }
				});
			}else{
				this.authentication = true
				this.code = ''
			}
		},
		
		
		// 重新发送验证码
		AnewSendCode(){
			let data = {
				mobile:uni.getStorageSync('storage_mobile')
			}
			requireurl.request('/users/codes/',data,this.Anewsuccess)
		},
		
		//重新发送回调
		Anewsuccess(e){
			if(e.statusCode === 200){
				this.getcode()
			}
		},
		
		getFile(){
			console.log('验证码错误')
		},
		
		
		//关闭错误验证码弹窗
		cancel(){
			this.authentication = false
		}
	},
	mounted() {
		this.getcode()
	},
	destroyed() {
		if(this.time){
			clearInterval(this.time)
		}
	},
	watch:{
		code(news,olds){
			console.log(news.length)
			if(news.length === 6){
				this.sedeCode()
			}
		}
	}
}
